@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.25em;
    height: 3em;
    font-weight: 500;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
    }
}

.m-btn-primary{
    color: #fff;
    background-color: $primary;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
    }
}

.m-btn-black-outline{
    border: 1px solid $graphite;
    color: $dark-gray;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
        border-color: darken($primary,10%);
    }
}

.m-btn-gray-outline{
    border: 1px solid #d0d5dd;
    color: $dark-gray;
    &:hover{
        color: #fff;
        background-color: darken($primary,10%);
        border-color: darken($primary,10%);
    }
}
