.about{
  padding: 3em 0 6em;
  margin-bottom: 3em;
  &__grid{
    margin-top: 4em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2em));
    gap: 3em 4em;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__items{
    @include grid100gap(3em);
  }
  &__desc{
    display: none;
  }
}

.about-item{
  display: flex;
  &__icon{
    width: 3em;
    display: block;
    flex-shrink: 0;
    margin-right: 1em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: .625em;
  }
  &__title{
    font-weight: 500;
    font-size: 1.25em;
    line-height: 150%;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .5em;
  }
}

.docs{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
  &__desc{
    margin-top: 1.25em;
    color: #667085;
  }
}

.doc-item{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-weight: 500;
  &__title{
    text-transform: uppercase;
    margin-bottom: .4em;
  }
  &__format{
    color: #667085;
  }
  &__link{
    margin-top: 1.25em;
    font-size: 1em;
    line-height: 150%;
    color: $primary;
    &:hover{
      color: $dark-gray;
    }
  }
}

.tariffs{
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(1.25em);
  }
  &__desc{
    color: #667085;
  }
  &__nav{
    display: flex;
    .m-btn-black-outline{
      &:before{
        content: '';
        flex-shrink: 0;
        width: .5em;
        height: .5em;
        margin-right: .5em;
        border-radius: 50%;
        background-color: $primary;
        transition: inherit;
      }
      &:hover{
        &:before{
          background-color: #fff;
        }
      }
    }
  }
  &__btn{
    margin-left: .75em;
  }
}


@media screen and (max-width: 992px){
  .about{
    padding: 2em 0;
    margin: 0;
    &__grid{
      margin-top: 3em;
      grid-template-columns: 100%;
    }
    &__items{
      gap: 2.5em;
    }
    &__desc{
      display: block;
      margin-top: 1em;
    }
  }

  .docs{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__col{
      grid-column: 1/-1;
      padding-bottom: 1.5em;
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .tariffs{
    &__inner{
      display: block;
    }
    &__info{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__btn{
      &:first-child{
        margin-left: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .about-item{
    &__icon{
      width: 2.5em;
    }
    &__content{
      padding-top: .375em;
    }
    &__title{
      font-size: 1.125em;
      line-height: 156%;
    }
    &__desc{
      margin-top: .25em;
    }
  }

  .docs{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .tariffs{
    &__nav{
      margin-top: 3em;
      @include grid100gap(.75em);
    }
    &__btn{
      margin: 0;
    }
  }
}
