.footer{
  padding: 3em 0;
  &__top{
    padding-top: 4em;
    border-top: 1px solid #828282;
  }
  &__logo{
    width: 8.875em;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  .nav{
    margin-top: 2em;
    flex-direction: row;
  }
  &__bottom{
    margin-top: 4em;
    padding-top: 2em;
    border-top: 1px solid #e4e7ec;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    color: #98a2b3;
  }
  &__c{
    font-weight: 500;
    margin-right: auto;
  }
  &__link{
    margin-left: 1em;
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 0 0 3em;
    .nav{
      &__link{
        margin: 0 auto 0 0 !important;
        display: inline-block;
      }
    }
    &__bottom{
      margin-top: 3em;
      flex-direction: column;
      align-items: flex-start;
    }
    &__c{
      order: 2;
    }
    &__link{
      margin: 0 0 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    padding: 0 0 3em;
    .nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: .75em 1em;
    }
  }
}

