.main{
  margin-top: 5em;
  padding: 6em 0 3em;
  text-align: center;
  overflow: hidden;
  &__desc{
    width: 48em;
    max-width: 100%;
    margin: 1.5em auto 0;
  }
  &__bottom{
    margin-top: 4em;
    position: relative;
    .video{
      position: relative;
      z-index: 4;
    }
    &._animated{
      .line{
        transform: translateX(0);
      }
    }
  }
}

.line{
  position: absolute;
  transition: all .4s;
}

.line1,
.line2,
.line3{
  transform: translateX(150%);
  right: 0;
}

.line4,
.line5{
  transform: translateX(-150%);
  left: 0;
}

.line1{
  height: 8.125em;
  top: -4em;
}

.line2{
  top: -.125em;
  height: 9.8125em;
  transition-delay: .5s;
}

.line3{
  height: 8.1875em;
  top: 2.25em;
  transition-delay: 1s;
}

.line4{
  height: 8.125em;
  top: 9em;
  transition-delay: 1.5s;
}

.line5{
  height: 8.125em;
  top: 14.75em;
  transition-delay: 2s;
}

.lines{
  display: none;
}

.video{
  margin: 0 auto;
  width: 57.25em;
  max-width: 100%;
  box-shadow: 0 2em 4em -.75em rgba(16, 24, 40, 0.2);
  video{
    display: block;
    width: 100%;
  }
}

.m-section{
  padding: 3em 0;
}

.info-block{
  @include grid100gap(.75em);
  color: $black;
  &__desc{
    font-size: 1.5em;
    font-weight: 600;
    text-transform: uppercase;
    color: $graphite;
  }
}

.info{
  &__desc{
    margin-top: 1.25em;
    @include grid100gap(1.5em);
    max-width: 48em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
    gap: 1.5em;
    margin-top: 4em;
  }
  &__desc2{
    margin-top: 3em;
    max-width: 48em;
  }
}

.info-card{
  border: .25em solid $graphite;
  padding: 1.25em;
  &__title{
    font-size: 1.25em;
    line-height: 150%;
    min-height: 3em;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__desc{
    margin-top: .5em;
  }
}

.text{
  ul,ol,
  p{
    @include lastM(0 0 1em);
  }
  li,
  p{
    font-size: 1.25em;
    line-height: 150%;
  }
  ul,ol{
    padding-left: .5em;
  }
  li{
    padding-left: 1.2em;
    position: relative;
    &:before{
      content: 'â€¢';
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  ol{
    counter-reset: section;
    li{
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
        position: absolute;
        width: auto;
        height: auto;
        background-color: transparent;
        font-variant-numeric: tabular-nums;
        -webkit-font-feature-settings: "tnum";
        font-feature-settings: "tnum";
      }
    }
  }
}


@media screen and (max-width: 992px){
  .main{
    margin-top: 4.5em;
    padding: 4em 0;
    margin-bottom: 2em;
    &__desc{
      margin-top: 1em;
    }
  }

  .m-section{
    padding: 2em 0;
  }

  .info-block{
    &__desc{
      font-size: .875em;
    }
  }

  .info{
    &__desc{
      margin-top: 1em;
    }
    &__items{
      grid-template-columns: repeat(2,calc(50% - .75em));
      margin-top: 3em;
    }
  }

  .info-card{
    padding: 1em;
    &__title{
      font-size: 1.125em;
      line-height: 156%;
      min-height: 3.12em;
    }
    &__desc{
      margin-top: .25em;
    }
    &__desc2{
      margin-top: 4em;
    }
  }

  .text{
    li,
    p{
      font-size: 1.125em;
      line-height: 156%;
    }
  }
}


@media screen and (max-width: 576px){
  .info{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .line,
  .line2,
  .line3,
  .line4,
  .line5{
    display: none;
  }

  .lines{
    position: absolute;
    width: 100%;
    bottom: .25em;
    top: 0;
    display: block;
  }
}
