.header{
  padding: 1em 0;
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  background-color: #fff;
  &._fix{
    box-shadow: 0 .25em .25em rgba(35, 37, 44, 0.05), 0 1em .75em rgba(35, 37, 44, 0.03);
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    width: 8.875em;
    margin-right: 2.5em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  flex-grow: 1;
  display: flex;
  align-items: center;
  &__scroll{
    flex-grow: 1;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    font-size: 1em;
    line-height: 150%;
    @include lastM(0 2em 0 0);
  }
}

.burger{
  display: none;
}

.header-contacts{
  &__item{
    display: grid;
    grid-template-columns: 10em 17.3125em;
    &-title,
    &-link{
      font-size: 1em;
      line-height: 150%;
    }
    &-link{
      font-weight: 900;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    border-bottom: 1px solid #f2f4f7;
    &__inner{
      justify-content: space-between;
    }
    &__logo{
      position: relative;
      z-index: 11;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
  }

  .burger{
    position: relative;
    width: 2.5em;
    height: 2.5em;
    display: flex;
    margin-right: -.25em;
    z-index: 11;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
    }
  }

  .menu {
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #fff;
    padding: 4.5em 0 0;
    z-index: 10;
    &__scroll {
      border-top: 1px solid #f2f4f7;
      padding: 2em 1em;
      display: flex;
      flex-direction: column;
      justify-content: unset;
      align-items: flex-start;
      overflow-y: auto;
      height: 100%;
    }
  }

  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      @include lastM(0 0 1em);
    }
  }

  .header-contacts{
    padding-top: 2em;
    margin-top: auto;
  }
}


@media screen and (max-width: 576px){
  .header-contacts{
    @include grid100gap(.5em);
    &__item{
      grid-template-columns: 100%;
    }
  }
}
