.req{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 2em));
    gap: 3em 4em;
    align-items: center;
  }
  &__img{
    width: 100%;
    display: block;
  }
  &__desc{
    color: #667085;
    margin-top: 1.5em;
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
  }
  &__btn{
    margin-right: .75em;
  }
}

.contacts{
  margin-top: 3em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 3em;
  }
  &__desc{
    margin-top: 1.25em;
    color: #667085;
  }
}

.contacts__items,
.addresses{
  @include grid100gap(2em);
}

.address-item{
  @include grid100gap(.5em);
}

.contact-item{
  @include grid100gap(.5em);
  color: $graphite;
  &__wrap{
    display: flex;
    align-items: center;
  }
  &__email,
  &__phone{
    font-size: 1.5em;
    font-weight: 600;
  }
  &__email{
    color: $primary;
    &:hover{
      color: $black;
    }
  }
  &__socials{
    display: flex;
    margin-left: 1em;
    &-item{
      @include lastM(0 .3125em 0 0);
      @include center;
      width: 1.5em;
      &:hover{
        opacity: .8;
      }
      img{
        width: 100%;
        display: block;
      }
    }
  }
}

.error404{
  padding: 2em 1em;
  flex-grow: 1;
  @include center;
  flex-direction: column;
  text-align: center;
  &__icon{
    width: 3.5em;
    margin-bottom: 1.5em;
  }
  &__desc{
    width: 32em;
    max-width: 100%;
    margin-top: 1em;
    font-weight: 600;
  }
  &__link{
    margin-top: 3em;
  }
}


@media screen and (max-width: 992px){
  .req{
    &__grid{
      grid-template-columns: 100%;
    }
    &__desc{
      margin-top: 1em;
    }
    &__nav{
      margin-top: 2em;
    }
  }

  .contacts{
    margin-top: 0;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
    &__col{
      grid-column: 1/-1;
      padding-bottom: 1.5em;
    }
    .h2{
      br{
        display: none;
      }
    }
    &__desc{
      margin-top: 1em;
    }
  }

  .contacts__items,
  .addresses{
    gap: 1.5em;
  }

  .error404{
    &__icon{
      margin-bottom: 1em;
    }
    &__link{
      margin-top: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .req{
    &__nav{
      @include grid100gap(.75em);
    }
    &__btn{
      margin: 0;
    }
  }

  .contacts{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .error404{
    &__link{
      width: 100%;
    }
  }
}
