.objects{
  &__tabs,
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__tabs{
    margin-top: 2em;
    padding-bottom: 15.125em;
    position: relative;
  }
  &__items{
    display: none;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 15.125em;
    padding: 4em 0;
    text-align: center;
  }
  &__item{
    &-num{
      font-weight: 900;
      font-size: 3.125em;
      line-height: 144%;
      letter-spacing: -0.02em;
      span{
        font-weight: 500;
      }
    }
    &-title{
      font-weight: 500;
      font-size: 1.125em;
      line-height: 156%;
      color: #667085;
      margin-top: .67em;
    }
  }
}

.object-tab{
  border: .625em solid $primary;
  background-color: $primary;
  padding: .875em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  color: $paper-gray;
  transition: all .3s;
  cursor: pointer;
  position: relative;
  height: 100%;
  &._type2{
    background-color: $neon-green;
    border-color: $neon-green;
    color: $dark-gray;
  }
  &._type3{
    background-color: $graphite;
    border-color: $graphite;
    .object-tab__desc{
      color: $neon-green;
    }
  }
  &:hover{
    transform: scale(1.05);
  }
  &._active{
    background-color: transparent;
    color: $dark-gray;
    pointer-events: none;
    .object-tab__bottom{
      opacity: 1;
    }
    &~.objects__items{
      display: grid;
    }
  }
  &__title{
    font-size: 1.5em;
    line-height: 133%;
    text-transform: uppercase;
    font-weight: 600;
  }
  &__desc{
    font-size: 1em;
    line-height: 150%;
    font-weight: 900;
    margin-top: auto;
    padding-top: .5em;
    text-transform: uppercase;
    color: $graphite;
  }
  &__bottom{
    position: absolute;
    right: -.625em;
    bottom: -2.375em;
    opacity: 0;
    transition: all .3s;
  }
  &__num{
    font-size: 3.75em;
    line-height: 167%;
    letter-spacing: -0.05em;
    font-weight: 900;
    span{
      font-weight: 500;
    }
  }
}

.map{
  height: 32.25em;
}


@media screen and (max-width: 992px){
  .objects{
    &__tabs{
      font-size: .625em;
    }
  }

  .map{
    height: 25em;
  }
}


@media screen and (max-width: 576px){
  .objects{
    &__tabs{
      font-size: 1em;
      padding-bottom: 0;
      margin-top: 1.5em;
    }
    &__tabs,
    &__items{
      grid-template-columns: 100%;
    }
    &__items{
      position: static;
      height: auto;
      margin-top: 1.5em;
    }
  }

  .object-tab{
    height: unset;
    min-height: 10.75em;
    &__bottom{
      right: -.5em;
      bottom: -2.125em;
    }
    &__num{
      font-size: 3em;
    }
  }

  .map{
    height: 15em;
    margin-top: 2em;
  }
}
