.fg {
  position: relative;
  margin: 0;
  label{
    position: absolute;
    left: 0;
    top: -.77em;
    display: block;
    font-size: 1.125em;
    line-height: 156%;
    color: #667085;
    font-weight: 500;
    span{
      color: $primary;
    }
  }
  input,
  textarea{
    border-radius: 0;
    border: none;
    border-bottom: 1px solid rgba(#000,.1);
    padding: 0;
    height: 2.92em;
    width: 100%;
    font-size: 1.5em;
    font-weight: 600;
    background-color: transparent;
    color: #000;
    &::placeholder {
      color: #929798;
    }
    &:focus {
      border-color: #9CA0A0;
    }
    &:disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      color: rgba(#000,.4);
    }
  }
  textarea{
    padding-top: 1.19em;
    padding-bottom: 1.19em;
    height: 5.83em;
    resize: none;
  }
  &._error{
    input,
    textarea{
      border-color: #FE6645;
      color: #FE6645;
    }
    .fg__error-text{
      display: block;
    }
  }
  &__hint,
  &__error-text{
    position: absolute;
    right: 0;
    top: -.42em;
    font-size: .75em;
    line-height: 167%;
    letter-spacing: -0.03em;
  }
  &__error-text{
    color: #FE6645;
    display: none;
  }
  &__hint{
    color: #0075FF;
  }
}



