.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(47, 50, 59, 0.75);
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 75.25em;
        max-width: calc(100% - 1em);
        position: relative;
        z-index: 3;
        padding: 2em 5em;
        display: inline-block;
    }
}

.modal-form{
    margin-top: 1.25em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 3em 1.875em;
    &__nav{
        margin-top: -.5em;
        display: flex;
        justify-content: center;
    }
    &__hint{
        margin-top: -.5em;
        color: #676f7b;
        opacity: .8;
        letter-spacing: -0.04em;

    }
}

._full{
    grid-column: 1/-1;
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {
            padding: 2em 1em;
        }
    }

    .modal-form{
        margin-top: 2em;
        grid-template-columns: 100%;
        gap: 2em;
    }
}


@media screen and (max-width: 576px){
    .modal{
        &:after{
            display: none;
        }
        &__dialog {

        }
    }

    .modal-form{
        .fg{
            &__hint{
                position: static;
            }
        }
        &__btn{
            width: 100%;
        }
    }
}
